
.down-index{
    background: var(--gray);
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    justify-content: center;
    padding: 2% 10%;
  }
  .down-index h5{
    margin: 10% 0;
  } 
  
  .down-index h4{
    margin: 10% 0;
  }
  .down-index h6{
    margin: 5% 0;
  }
  .down-index section{
    border-left: solid 1px white;
    padding: 0 10%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* align-self: center; */
  }
  
  
  section h4, section h6 , section h5{
    color: white;
  }
  
  .rights {
    text-align: center;
    color: white;
  }

  
.icon i {
    display: inline-block; 
    margin-right: 5px;
  }
.icon {/*a items*/
    color: white;
    text-decoration: none;
    display: flex;
    align-items: center;
    margin: 5% 5% 5% 0  ;
}

.icon i:before{
    border-radius: 100%;
    color: white;
    font-size: 30px;
    font-weight: 500;
    font-family: Flaticon;
}

.jugger_logo figure{
    max-width: 50%;
}